import React, { useEffect } from 'react';
import { Canvas } from './Canvas';

export interface NodeType {
    val: string | number;
}

export const Node: React.FC<NodeType> = (props) => {
    const { val } = props;
    const randomNumberX = Math.round(Math.random() * (100 - 300) + 300);
    const randomNumberY = Math.round(Math.random() * (100 - 300) + 300);
    useEffect(() => {
        console.log(randomNumberY);
        const ctx: any = document?.getElementById('myCanvas')?.getContext('2d');
        ctx.beginPath();
        ctx.arc(randomNumberX, randomNumberY, 50, 0, Math.PI * 2, true);
        ctx.lineWidth = 2;
        ctx.stroke();
        ctx.font = '30px Arial';
        ctx.fillText(val, randomNumberX - 10, randomNumberY + 10);
    }, []);

    return (
        <>
            <Canvas></Canvas>
        </>
    );
};
